.App {
  text-align: center;
}

body {
  margin: 0;
  padding: 0;
}

.inputs {
  height: 400px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.inputs input {
  width: 200px;
  height: 50px;
  font-size: 15px;
  margin: 10px;
  padding-left: 10px;
}

.inputs button {
  border: none;
  width: 150px;
  height: 50px;
  background-color: rgb(47, 47, 47);
  color: white;
  border-radius: 10px;
  font-size: 15px;
  margin: 10px;
}

.inputs button:hover {
  cursor: pointer;
}

.rules1 {
  padding: 50px 50px 50px 50px;
}

button {
  margin: 8px;
}

footer {
  background-color: rgb(47, 47, 47);
  margin: auto -16px;
  padding: 16px 32px;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
}

footer p {
  color: #ccc;
}
